<template>
  <v-app>
    <div id="mainpage">
      <router-view></router-view>
    </div>
  </v-app>
</template>

<script>
import axios from 'axios';
export default {
  name : 'main',
  components: {
  },
  created(){

    // 192.168.100.73  DB W
    // 192.168.100.80  DB 
    // 192.168.1.6  DB  
    // 192.168.1.7  DB 
    // 192.168.1.2  DB 
    axios.defaults.baseURL = 'http://localhost:9999'; //6  7 

    //   vue-session jwt  
    const jwt = this.$session.get('jwt');
    console.log(jwt);
    //axios default Authorization  jwt
    if(jwt){
        axios.defaults.headers.common['Authorization'] = jwt;

        //jwt   userId, no, type vuex   
        axios.get('/user/mypage').then((Response)=>{
                this.$store.dispatch('Re_userInfo',Response.data)
            })
        console.dir(this.$store);
        console.dir(axios.defaults.headers);
    }
    axios.defaults.withCredentials = true;//CORS    


  },
}
</script>

