<template>
  <div id="navheader">
    <v-toolbar>
      <!--  -->
      <!-- MY -->
      <div v-if="userInfo" id="login">
        <!--  -->
        <router-link id="logo" to="/" style="text-decoration: none; color:black"><h1><b>SSAZIT</b></h1></router-link>
        <div v-if="userInfo.blogNo==0" @click="blogcreate">
          <b-button variant="link" style="text-decoration: none;">
            <router-link to="/blogcreate" style="text-decoration: none; color:black"> </router-link>
          </b-button>
        </div>
        <!-- MY -->
        <div v-if="userInfo.blogNo!=0" @click="myblog">
          <b-button variant="link" style="text-decoration: none;">
            <router-link :to="{path: `/bloghome/${userInfo.userId}`}" style="text-decoration: none; color:black">My </router-link>
          </b-button>
        </div>

        <!--  -->
        <div @click="rank">
          <b-button variant="link" style="text-decoration: none;">
            <router-link to="/rank" style="text-decoration: none; color:black"></router-link>
          </b-button>
        </div>
        

        <!--  -->
        <div class="my-3">
          <b-button id="popover-reactive-2" variant="link" style="text-decoration: none; color:black">
            
          </b-button>
        </div>
        <b-popover
          target="popover-reactive-2"
          triggers="hover"
          placement="bottom"
        >
          <template v-slot:title>
            
            {{ }} 
            <br>
            
            {{ }} 
          </template>
          <router-link to="/friends" style="text-decoration: none; color:black"></router-link>
        </b-popover>
        

        
        <!--  -->
        <div class="my-3">
          <b-button id="popover-reactive-3" variant="link" style="text-decoration: none; color:black">
            
          </b-button>
        </div>
        <b-popover
          target="popover-reactive-3"
          triggers="hover"
          placement="bottom"
        >
          <template v-slot:title>
            {{ }}
            <br>
            {{ }}
            <br>
            {{ }}
          </template>
          <router-link to="/bookmark" style="text-decoration: none; color:black"></router-link>
        </b-popover>
        

        <!--  -->
        <div class="my-3">
          <b-button id="popover-reactive-5" variant="link" style="text-decoration: none; color:black">
            
          </b-button>
        </div>
        <b-popover
          target="popover-reactive-5"
          triggers="hover"
          placement="bottom"
        >
          <template v-slot:title>
            {{ }}Groot   <br>
            {{ }}Hundred    
          </template>
        </b-popover>
        
        <!--  -->
        <div @click="messages">
          <b-button variant="link" style="text-decoration: none;">
          <router-link to="/messages" style="text-decoration: none; color:black"></router-link>
          </b-button>
        </div>

        <!--  -->
        <div class="my-3">
          <b-button id="popover-reactive-4" variant="link" style="text-decoration: none; color:black">
            {{ userInfo.userName }} 
          </b-button>
        </div>
        <b-popover
          target="popover-reactive-4"
          triggers="hover"
          placement="bottom"
        >
          <template v-slot:title>
            <router-link to="/mypage" style="text-decoration: none; color:black;">
              <v-avatar>
                <img :src="userImage">
              </v-avatar>
              <b> {{ userInfo.userName }}</b>
            </router-link>
            <v-btn small rounded outlined color="info" @click="Logout" style="margin:0 0 0 10px;"></v-btn>
          </template>
        </b-popover>


        <!--  -->
      </div>
      
      <div v-else id="non_login">
        <!--  -->
        <router-link id="logo" to="/" style="text-decoration: none; color:black"><h1><b>SSAZIT</b></h1></router-link>
        <b-button variant="link" style="text-decoration: none;">
          <router-link to="/login" style="text-decoration: none; color:black"></router-link>
        </b-button>
        <b-button variant="link" style="text-decoration: none;">
          <router-link to="/join" style="text-decoration: none; color:black"></router-link>
        </b-button>
      </div>
    </v-toolbar>
    
  </div>
</template>

<script>
export default {
  name: "NavHeader",
  data() {
    return {
      logo: require("../images/logo.png"),
      userImage: '',
    };
  },
  created(){
    this.userImage = this.serverURL + this.userInfo.profileImg;
  },
  computed: {
    userInfo() {
      return this.$store.getters.userInfo;
    },
    serverURL(){
      return this.$store.getters.serverURL;
    }
  },
  methods: {
    Logout() {
      this.$router.push('/logout');
    },
    blogcreate(){
      this.$router.push("/blogcreate");
    },
    myblog() {
      this.$router.push({path: `/bloghome/${this.userInfo.userId}`});
    },
    rank(){
      this.$router.push("/rank");
    },
    messages(){
      this.$router.push("/messages");
    },
  }
};

</script>

<style scoped>
li {
  margin: 0 10px;
}
div {
  display: inline;
}
#logo {
  position: fixed;
  top: 10px;
  left: 100px;
}
#login {
  position: fixed;
  top: 15px;
  right: 100px;
}
#non_login {
  position: fixed;
  top: 20px;
  right: 100px;
}

</style>