<template>
    <div id="editor">
        <div class="editor_header">
            <span> : </span> <input type="text" v-model="title">
            <select id="articleType" v-model="articleType">
                <option disabled hidden value="default">== ==</option>
                <option value="N"></option>
                <option value="A"></option>
            </select>
            <select id="boardList" v-model="boardNo">
                <option disabled hidden value="default">== ==</option>
            </select>
            <select id="openScope" v-model="openScope">
                <option value="public"> </option>
                <option value="protected"> </option>
                <option value="private"></option>
            </select>
            <label><input type="checkbox" value="sharable" v-model="sharable"> </label>
            <label><input type="checkbox" value="reviewRequest" v-model="reviewRequest"> </label>
        </div>

        <ckeditor v-model="editorData" :config="editorConfig"></ckeditor>
        <button @click="writeArticle"></button>
    </div>
</template>
<script>
import axios from 'axios';
export default {
    data() {
        return {
            editorData: '<p>Content of the editor.</p>',
            editorConfig: {
                // The configuration of the editor.
            },
            title: '',
            articleType: 'default',
            boardNo: '',
            openScope: 'public',
            sharable: '',
            reviewRequest: ''
        };
    },
    methods:{
        writeArticle(){
            axios.post("/article/article", {
                boardNo: this.boardNo,
                title: this.title,
                content: this.editorData,
                scope: this.openScope,
                articleType: this.articleType,
                sharable: this.sharable,
                reviewRequest: this.reviewRequest
            })
            .then(() => {
                alert("");
            })
            .catch(() =>{
                alert("  ");
            })
        }
    }
}
</script>
