<template>
  <div id="article-create">
    <div class="article-setting" style="height:100%;">
      <v-row class="pl-3" style="justify-content:start;">        
        <h4 class="text-justify font-weight-bold m-0" style="vertical-align:middle;">
          <div style="height:10px;"></div>
          
        </h4>
        <v-btn-toggle
          v-model="icon"
          borderless
        >
          <v-btn  @click="writeArticle" value="" color="white" class="text-decoration-none">
            <span class="hidden-sm-and-down mr-1"></span>
            <v-icon small>mdi-pencil</v-icon>
          </v-btn>
        </v-btn-toggle>
      </v-row>
      <br>

      <div class="article-setting-base" style="width:1050px;">
        <v-row class="pl-3">
          <v-col class="d-flex p-0 mr-2" cols="2">
            <v-select
              v-model="boardNo"

              :items="boardItems"
              item-value="boardNo" 
              item-text="boardName"
              label=""
              outlined
              dense
            ></v-select>
          </v-col>
          <v-col class="d-flex p-0 mr-2" cols="2">
            <v-select
              v-model="article.openScope"

              :items="openItems"
              item-value="value" 
              item-text="name"
              label=""
              outlined
              dense
            ></v-select>
          </v-col>

          <span class="pt-1 mx-2"> </span>
          <v-switch v-model="shareSwitch" class="mt-0"></v-switch>
          <span class="pt-1 mx-2"> </span>
          <v-switch v-model="questionSwitch" class="mt-0 mr-2"></v-switch>

          <v-col class="pt-0" cols="3" v-if="boardType == 'N'">
            <v-combobox
              v-model="catSelected"

              :items="categoryItems"
              item-value="categoryNo" 
              item-text="categoryName"
              label=" ."
              class="p-0"
            ></v-combobox>
          </v-col>
        </v-row>

        <v-row class="px-3" v-if="boardType == 'A'">
          <v-col class="d-flex p-0 mr-2" cols="2">
            <v-select
              v-model="siteSelected"

              :items="siteList"
              label=""
              outlined
              dense
            ></v-select>
          </v-col>
          <v-col v-if="siteSelected != ''" class="p-0">
            <v-combobox
              v-model="problemSelected"

              :items="acategorys"
              item-value="acategoryNo"
              item-text="title"
              label=" "
              class="p-0"
            >
              <template slot='selection' slot-scope='{ item }'>
                {{ item.acategoryNumber }} - {{ item.title }}
              </template>
              <template slot='item' slot-scope='{ item }'>
                {{ item.acategoryNumber }} - {{ item.title }}
              </template>
            </v-combobox>
          </v-col>
          <v-col class="p-0" v-else>
            <v-text-field 
              outlined
              clearable
              dense
              placeholder=" "
              
              v-model="acategory.acategorySource"
            ></v-text-field>
            <v-text-field
              outlined
              clearable
              dense
              placeholder=" "

              v-model="acategory.acategoryNumber"
            ></v-text-field>

            <v-text-field
              outlined
              clearable
              dense
              placeholder=" "

              v-model="acategory.title"
            ></v-text-field>
            <v-text-field
              outlined
              clearable
              dense
              placeholder=" "

              v-model="acategory.link"
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row class="px-3">
          <div>
            <div style="height:10px;"></div>
            <span class="mr-3"></span>
          </div>
          <v-col class="p-0">
            <v-text-field
              v-model="article.title"
              outlined
              clearable
              dense
              placeholder=" "
            ></v-text-field>
          </v-col>
        </v-row>


        <v-row class="pl-3">
          <div>
            <div style="height:3px;"></div>
            <span class="mr-3"> </span>
          </div>
          <v-col cols="11" class="p-0">
            <v-combobox
              v-model="tagSelected"
              
              :items="tagItems"
              item-value="tagNo" 
              item-text="tagName"
              label=" . ( enter)"
              multiple
              chips
              class="p-0 m-0"
            ></v-combobox>
          </v-col>
        </v-row>

        <ckeditor v-model="editorData" :config="editorConfig"></ckeditor>
        <!--  [] -->
        <div align="right" class="my-2 mx-0">
          <!--  !! -->
          <v-btn text color="error"> </v-btn>
        </div>

        <hr>
      </div>
    </div>
  </div>

</template>

<script>
import axios from 'axios';
export default {
  name: "BlogEditor",
  data() {
    return {
      article:{
        boardNo: '',
        title: '',
        content: '',
        openScope: '',
        thumbNail: '',
        articleType: '',
        sharable:'',
        reviewRequest: '',
      },
      boardNo:'',
      boardType:'',
      shareSwitch: true,
      questionSwitch: false,
      editorData: '<p>Content of the editor.</p>',
      editorConfig: {
          extraPlugins: 'prism',
					codeSnippet_theme: 'monokai_sublime',
      },
      //     
      boardItems: [
        ],
      openItems: [
        {
          value:'public',
          name:''
        },
        {
          value:'protected',
          name:''
        },
        {
          value:'private',
          name:''
        }
      ],
      siteList: [],
      siteSelected: "none",

      catSelected: '',
      tagSelected: [],
      problemSelected: '',

      
      acategory:{
	      acategorySource: '',			/*NN   */
        acategoryNumber: '',			/*NN   */
	      title: '',						/*NN  */
	    	link: ''						/* */
      },
      arttag: [],
      tmptag:[],

      tagItems: [],
      categoryItems: [ ],

      acategorys: [],
    };
  },
  watch:{
    siteSelected: function(){
      axios.get(`/article/acategorys?acategorySource=${this.siteSelected}`)
      .then( ({data}) => {
          this.acategorys = data;
      })
      .catch( () => {

      })
    },
    boardNo: function(){
      this.article.boardNo = this.boardNo;

      for(let board of this.boardItems){
        if(board.boardNo == this.boardNo){
          this.boardType = board.boardType;
          break;
        }
      }
    }
  },
  methods:{
    displayTitle(){
      alert("sssss");
    },
    writeArticle(){
      this.article.content = this.editorData;
      //  
      // this.article.articleType = "N";     articleType
      this.article.sharable = this.shareSwitch? 1 : 0;
      this.article.reviewRequest = this.reviewRequest? 1 : 0;
      
  
      if(this.boardType == 'A' && this.siteSelected != '' && ( !(this.problemSelected instanceof Object) || !this.problemSelected.acategoryNo ) ){
        alert("   !");
        return;
      }      
      
      // 
      for(let tag of this.tagSelected){
        if(tag instanceof Object && tag.tagNo){
          this.arttag.push({tagNo : tag.tagNo});
        }else{
          this.tmptag.push({tmptagName : tag});
        }
      }



      console.log("==============")
      console.dir(this.arttag);
      console.dir(this.tmptag);

      if(this.siteSelected == ''){
        this.problemSelected = null;
      }
      
      axios.post("/article/CUD",{
        article: this.article,
        arttag: this.arttag,
        tmpTag: this.tmptag,
        artCategory: {category: this.catSelected.categoryNo},
        artAlgo: {acategoryNo: this.problemSelected.acategoryNo},
        acategory: this.acategory
      })
    }
  }
  ,
  created(){
    axios.get(`/blog/myboards?blogNo=${this.$store.getters.blogInfo.blogNo}`)
    .then(({data}) => {
      this.boardItems = data;
    })
    .catch(() => {
      alert("    .")
    })

    axios.get("/article/categorys")
    .then(({data}) => {
      this.categoryItems = data;
      console.dir(this.categoryItems)
    })
    .catch(() => {
      alert("    .")
    })

    axios.get("/tag/tags")
    .then(({data}) => {
      this.tagItems = data;
      console.dir(this.tagItems)
    })
    .catch(() => {
      alert("    .")
    })

    axios.get("/article/acategorys/site")
    .then(({data}) => {
      this.siteList = data;
      this.siteList.push('');
    })
    .catch(() =>{
      alert("    .")
    })
    
  },
  components: {}
};
</script>

<style></style>
